import React from 'react';
import Spinner from '../../sharedComponents/functionalComponents/spinner/Spinner';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

import axios from 'axios';

interface State {
  rows:any
}

class UsersPage extends React.Component {
  public state:State;
  private lastQuery:string = "";
  private cancelTokenSource:any;
  private rows:any;
  private useStyles:any;
  
  constructor(props) {
    super(props);
    this.state = {
      rows:[]
    };
  }
  
  componentDidMount() {
    console.log(localStorage.getItem("authToken"))
    if(!localStorage.getItem("authToken"))
      window.location.href = "/";
      
    this.loadUsers();
  }
  
  loadUsers() {
    const url = "/api/superadmin/users";
    if(this.cancelTokenSource)
      this.cancelTokenSource.cancel();
    this.cancelTokenSource = axios.CancelToken.source();

    axios.get(url, {
      cancelToken: this.cancelTokenSource.token
    })
    .then((d)=>{
      console.log("#######");
      console.log(d);
      let users = d.data;
      let rows:any = [];
      users.forEach((item, index)=>{
        console.log(item);
        rows.push(this.createData(item.uuid, item.username, item.verified.toString(), item.active.toString(), item.superAdmin.toString(), item.roles));
      });
      
      this.setState({rows:rows});
      
    })
    .catch((error) => {
      console.error(error);
    })
    .then(() => {
    });
  }

  createData(uuid, username, verified, active, superAdmin, roles) {
    return { uuid, username, verified, active, superAdmin, roles };
  }
  
  render() {
    return (
      <section>
       <div className="row">
        <div className="col-md-12">
          <TableContainer component={Paper}>
            <Table className={''} aria-label="simple table">
              <TableHead>
                <TableRow>
                  <TableCell>UUID</TableCell>
                  <TableCell align="left">User name</TableCell>
                  <TableCell align="left">Verified</TableCell>
                  <TableCell align="left">Active</TableCell>
                  <TableCell align="left">Superadmin</TableCell>
                  <TableCell align="left">Roles</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {this.state.rows.map((row, index) => (
                  <TableRow key={index}>
                    <TableCell component="th" scope="row">
                      {row.uuid}
                    </TableCell>
                    <TableCell align="left">{row.username}</TableCell>
                    <TableCell align="left">{row.verified}</TableCell>
                    <TableCell align="left">{row.active}</TableCell>
                    <TableCell align="left">{row.superAdmin}</TableCell>
                    <TableCell align="left">{row.roles}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </div>
       </div>
      </section>
    );
  }
}

export default UsersPage;